.carousel {
  --carousel-extra-space: 16px;

  position: relative;

  @include media-mobile-only {
    --carousel-spacing: #{$space*0.5};
    --items-number: 1.8;
    --space-between: #{$space*1};
  }

  @include media-tablet-portrait-up {
    --carousel-spacing: #{$space};
  }

  @include media-tablet-portrait-only {
    --items-number: 3.5;
    --space-between: #{$space*3};
  }

  @include media-tablet-landscape-only {
    --items-number: 5.5;
    --space-between: #{$space*5};
  }

  @include media-desktop-up {
    --items-number: 7;
    --space-between: #{$space*6};
  }
}


.carousel__list {
  display: flex;
  padding: 3rem var(--carousel-extra-space) 3rem;
  margin: -3rem calc(var(--carousel-extra-space) * -1) -3rem;
  list-style: none;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;

  &::after {
    content: "";
    flex-shrink: 0;
    width: var(--page-paddings);
    height: 1px;
  }

  // scroll-snap-type: x mandatory;
  // scroll-snap-type: x proximity;
  // scroll-behavior: smooth;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  // &::-webkit-scrollbar {
  //   width: $space*0.75;
  //   height: $space*0.75;
  // }

  // &::-webkit-scrollbar-thumb {
